
.helpModalView{
    position: absolute;
    z-index: 3;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.helpModal{
    background-color: rgb(0, 14, 31,0.7);
    position: fixed;
    height: 100vh;
    width: 100vw;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

.closeIcons{
    position: fixed;
    z-index: 2;
    bottom: 0%;
    right: 0%;
    height: auto;
    width: 16%;
    border-radius: 50%;
    background-color: rgb(241, 253, 255);
    margin: 1rem;
    max-width: 6rem;
}